import $ from 'jquery'
import 'jquery.scrollto'
import './jquery.homeycombs'
import 'jquery.easing'
import moment from 'moment'

const date = new Date(1418293867 * 1000)
console.log(moment)
console.log(moment())
console.log(moment().subtract(3, 'months'))
console.log(moment().subtract(1, 'days'))
console.log(moment(new Date(1418293867 * 1000)))
console.log(moment(date))

$(document).ready(function(){


  $('.honeycombs').honeycombs({
    combWidth: 220,
    margin: 10
  })

  const scrollTo = (to) => {
    $.scrollTo(to, 1000, { easing:'easeInOutExpo', offset:0, 'axis':'y' })
  }

  $('.home--btn').click((e) => {
    console.log('Clicked')
    scrollTo('body')
  })

  $('.about--btn').click((e) => {
    scrollTo('#about')
  })

  $('.team--btn').click((e) => {
    scrollTo('#team')
  })
})



